import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LayoutComponent } from './view/layout/layout.component';
import { LoginComponent } from './auth/login/login.component';
import { PasswordComponent } from './auth/password/password.component';
import { SignupComponent } from './auth/signup/signup.component';
import { PageNotFoundComponent } from './auth/page-not-found/page-not-found.component';

const routes: Routes = [
  { path: '', redirectTo: '/web/home', pathMatch: 'full' },
  { path: 'web', component: LayoutComponent, loadChildren: () => import('./web/web.module').then(a => a.WebModule) },
  { path: 'private', component: LayoutComponent, loadChildren: () => import('./private/private.module').then(a => a.PrivateModule) },
  { path: 'gazette', component: LayoutComponent, loadChildren: () => import('./blog/blog.module').then(a => a.BlogModule) },
  { path: 'login', component: LoginComponent },
  { path: 'signup', component: SignupComponent },
  { path: 'password', component: PasswordComponent },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      scrollPositionRestoration: 'enabled', // Scroll the page to top on changing page
    })
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }
