export class Blog {
    id: number;
    title: string;
    subtitle: string;
    content: string;
    category: string;
    date: string;

    constructor(id: number, title: string, subtitle: string, content: string, category: string, date: string) {
        this.id = id,
        this.title = title,
        this.subtitle = subtitle,
        this.content = content,
        this.category = category,
        this.date = date;
    }
}
