import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BlogContentComponent } from './blog-content/blog-content.component';
import { BlogItemComponent } from './blog-content/blog-item/blog-item.component';

import { HomeblogComponent } from './homeblog/homeblog.component';


const routes: Routes = [
    { path: '', redirectTo: '/gazette/blog', pathMatch: 'full' },
    { path: 'blog', component: HomeblogComponent, data: {animation: 'blogPage'} },
    { path: 'content', component: BlogContentComponent, data: {animation: 'contentPage'} },
    { path: 'item/:id', component: BlogItemComponent, data: {animation: 'itemPage'} }
];

@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    exports: [
        RouterModule
    ]
})
export class BlogRoutingModule { }
