import { Component, Renderer2 } from '@angular/core';
import { Event, NavigationCancel, NavigationEnd, NavigationError, Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent {

  mobileNavEnable = false;
  sideNav = true;
  sideText = '';

  constructor(private renderer: Renderer2,
              private router: Router
  ) {
    this.router.events.subscribe((event: Event) => {
      switch (true) {
        case event instanceof NavigationEnd:
        case event instanceof NavigationCancel:
        case event instanceof NavigationError: {
          this.mobileNavEnable = false;
          this.renderer.removeClass(document.body, 'mobile-nav-active');
          break;
        }
        default: {
          break;
        }
      }
      this.sideMenuToggle(event);
    });
   }

  public onMobileToggle(): void
  {
    if (this.mobileNavEnable)
    {
      this.renderer.removeClass(document.body, 'mobile-nav-active');
      this.mobileNavEnable = false;
    }
    else{
      this.renderer.addClass(document.body, 'mobile-nav-active');
      this.mobileNavEnable = true;
    }
  }

  private sideMenuToggle(event: Event): void
  {
    if (event instanceof NavigationEnd && (event.url === '/web/home' || event.url === '/'))
    {
      this.sideText = '';
    } else if (event instanceof NavigationEnd && event.url === '/web/persona') {
      this.sideText = 'Persona';
    } else if (event instanceof NavigationEnd && event.url === '/web/itinerary') {
      this.sideText = 'Itinerary';
    } else if (event instanceof NavigationEnd && event.url === '/web/eminent') {
      this.sideText = 'Eminent';
    } else if (event instanceof NavigationEnd && event.url === '/web/fascinate') {
      this.sideText = 'Fascinate';
    } else {
      this.sideText = 'Gazette';
    }
    if (event instanceof NavigationEnd && (event.url === '/web/home' || event.url === '/')) {
      this.sideNav = true;
    } else {
      this.sideNav = false;
    }
  }

}
