import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { PersonaComponent } from './persona/persona.component';
import { EminentComponent } from './eminent/eminent.component';
import { ItineraryComponent } from './itinerary/itinerary.component';
import { FascinateComponent } from './fascinate/fascinate.component';


const routes: Routes = [
    { path: '', redirectTo: '/web/home', pathMatch: 'full' },
    { path: 'home', component: HomeComponent, data: {animation: 'homePage'} },
    { path: 'persona', component: PersonaComponent, data: {animation: 'personaPage'} },
    { path: 'eminent', component: EminentComponent, data: {animation: 'eminentPage'} },
    { path: 'itinerary', component: ItineraryComponent, data: {animation: 'itineraryPage'} },
    { path: 'fascinate', component: FascinateComponent, data: {animation: 'fascinatePage'} }
];

@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    exports: [
        RouterModule
    ]
})
export class WebRoutingModule { }
